
@import url(http://fonts.googleapis.com/css?family=Merriweather+Sans);
@font-face{font-family:'bebas_neueregular';src:url("assets/fonts/bebasneue-webfont.woff2") format("woff2"),url("assets/fonts/bebasneue-webfont.woff") format("woff");font-weight:normal;font-style:normal
}
html{height:100%;background:url(./assets/GoldenGlitter.jpg) no-repeat center center fixed;background-size:cover
}
body{-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;height:100%;margin:0px;box-sizing:border-box;font-size:14px
}
ol li,p{font-family:Source Sans Pro, Helvetica, sans-serif
}
a.help{background:#666;border-radius:12px;color:#ffffff;display:inline-block;line-height:12px;-ms-flex-pack:center;justify-content:center;margin-left:3px;text-align:center;width:12px;font-size:12px;transition:all .3s ease-out;position:absolute;top:40%;font-size:0.5em
}
a.help:hover{background:#cf151b
}
#app{width:100vw;max-width:1200px;font-family:'bebas_neueregular', Source Sans Pro, Helvetica, sans-serif;text-align:center;font-weight:normal;margin:0 auto
}
#app a{text-decoration:none
}
h1{font-weight:normal;font-size:24px;color:#cf151b;letter-spacing:1.5px;margin:0;padding:15px;position:relative
}
.logo{-ms-flex-item-align:center;-ms-grid-row-align:center;align-self:center
}
.transparent{background-color:rgba(254,252,232,0.6);height:calc(100vh - 215px);min-height:400px;max-height:550px;box-shadow:rgba(255,206,84,0.5) 0px 0px 40px 2px
}
#main_block .transparent{display:-ms-flexbox;display:flex;position:relative;margin:0 auto;max-width:500px
}
#mainStage{margin:0 auto;-ms-flex-item-align:center;-ms-grid-row-align:center;align-self:center;-ms-flex-pack:center;justify-content:center;background-color:lightgrey;max-width:500px
}
#photo_block{position:relative
}
.content-block{color:white;padding:0 10px;overflow:hidden;height:100%
}
.wrapper,.header{display:-ms-flexbox;display:flex;-ms-flex-flow:row wrap;flex-flow:row wrap;font-weight:bold;text-align:center
}
.wrapper>*,.header>*{-ms-flex:1 100%;flex:1 100%
}
.header{width:100vw;max-width:1366px
}
.header img{max-width:100%
}
.footer img{max-width:100%;margin-top:20px
}
#caption{position:absolute;margin:5px auto;width:100%;opacity:0;text-shadow:rgba(0,0,0,0.5) 1px 3px 5px;transform:translateY(-50px);transition:all .3s ease-in-out .2s;background-color:#cf151b;height:20px
}
#caption.active{opacity:1;transform:translateY(0px)
}
.aside{-ms-flex:1;flex:1
}
.aside-1{-ms-flex-order:1;order:1
}
.main{-ms-flex-order:2;order:2;-ms-flex:2;flex:2
}
.aside-2{-ms-flex-order:3;order:3
}
.footer{-ms-flex-order:4;order:4
}
#social_action{position:absolute;bottom:40px;left:0;width:100%
}
.help-panel{position:absolute;top:0;left:0;width:100%;padding:20px 30px;box-sizing:border-box;color:black;background-color:rgba(251,247,216,0.9);width:100%;height:100%;position:absolute;-ms-flex-align:center;align-items:center;transition:opacity .25s ease-out;pointer-events:none;-ms-flex-direction:column;flex-direction:column;color:#3c3b3b
}
#help-layout{opacity:0;transition:opacity .25s ease-out;pointer-events:none
}
#help-layout.active{opacity:1;pointer-events:auto
}
#help-photos{opacity:0;transition:opacity .25s ease-out;pointer-events:none
}
#help-photos.active{opacity:1;pointer-events:auto
}
#help-layout-close{position:absolute;top:20px;right:10px
}
#help-photos-close{position:absolute;top:20px;right:10px
}
.scroll{width:90%;height:calc(100% - 66px);margin:0 auto;overflow-y:scroll;-webkit-overflow-scrolling:touch;max-height:100%;-ms-flex-pack:center;justify-content:center;display:-ms-flexbox;display:flex;-ms-flex-flow:row wrap;flex-flow:row wrap
}
.scroll img{max-width:100%
}
#scene-scroll img{display:block;width:100% !important;height:auto !important;max-width:200px
}
.scroll::-webkit-scrollbar{width:.3em
}
.scroll::-webkit-scrollbar-track{-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3)
}
.scroll::-webkit-scrollbar-thumb{background-color:red;outline:1px solid slategrey
}
.social{padding-left:45px;height:36px;background:none;border:none;display:block;background-size:35px 35px;background-position:left center;background-repeat:no-repeat;border-radius:4px;color:white;font-family:"Merriweather Sans", sans-serif;font-size:14px;margin-bottom:15px;width:205px;border-bottom:2px solid transparent;border-left:1px solid transparent;border-right:1px solid transparent;box-shadow:0 4px 2px -2px gray;text-shadow:rgba(0,0,0,0.5) -1px -1px 0;margin:8px auto;transition:all .25s ease-out
}
button#layout_btn{border-color:#2d5073;background-color:#3b5998;cursor:pointer;text-transform:uppercase;padding:0;font-family:'bebas_neueregular', Source Sans Pro, Helvetica, sans-serif;font-size:18px;letter-spacing:1px;background-color:#e8d69e;color:#cf151b;text-shadow:none
}
button#message_btn{border-color:#2d5073;background-color:#3b5998;cursor:pointer;text-transform:uppercase;font-family:'bebas_neueregular', Source Sans Pro, Helvetica, sans-serif;font-size:18px;letter-spacing:1px;padding:0;background-color:#e8d69e;color:#cf151b;text-shadow:none
}
button#facebook{border-color:#2d5073;background-color:#3b5998;background-image:url(http://icons.iconarchive.com/icons/danleech/simple/512/facebook-icon.png);cursor:pointer
}
button#facebook:hover{background-color:#4a6ea9
}
button#twitter{border-color:#83c0dc;background-color:#54bdee;background-image:url(http://icons.iconarchive.com/icons/danleech/simple/512/twitter-icon.png);cursor:pointer
}
button#twitter:hover{background-color:#32def4
}
a#dl{border-color:#83c0dc;background-color:#548ac3;background-image:url(../static/images/download_icon.svg);background-size:20% 80%;-ms-flex-align:start;align-items:flex-start;text-align:center;cursor:default;color:white;box-sizing:border-box;padding:8px 6px 8px 45px;cursor:pointer
}
a#dl:hover{background-color:#83c0dc
}
.fade-enter-active,.fade-leave-active{transition:opacity .5s
}
.fade-enter,.fade-leave-active{opacity:0
}
textarea{height:130px
}
#thanks{background-color:rgba(251,247,216,0.9);width:100%;height:100%;position:absolute;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;opacity:0;transition:opacity .25s ease-out;-moz-transition:opacity .25s ease-out;-webkit-transition:opacity .25s ease-out;pointer-events:none;-ms-flex-direction:column;flex-direction:column;color:#3c3b3b;padding:10px 45px;box-sizing:border-box
}
#thanks p{width:90%;text-align:center;margin:0 auto
}
#thanks h1{font-size:34px
}
#thanks.active{opacity:1;pointer-events:auto
}
#load-panel{background-color:rgba(0,0,0,0.5);width:100%;height:100%;position:absolute;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;opacity:0;transition:opacity .25s ease-out;-moz-transition:opacity .25s ease-out;-webkit-transition:opacity .25s ease-out;pointer-events:none
}
#load-panel.active{opacity:1
}
.spinner{margin:0 auto;width:70px
}
.spinner>div{width:18px;height:18px;background-color:#fbf7d8;border-radius:100%;display:inline-block;animation:sk-bouncedelay 1.4s infinite ease-in-out both
}
.spinner .bounce1{animation-delay:-0.32s
}
.spinner .bounce2{animation-delay:-0.16s
}
@keyframes sk-bouncedelay{
0%,80%,100%{transform:scale(0)
}
40%{transform:scale(1)
}
}
.dz-preview{visibility:hidden
}
figure{margin:0;padding:0;background:#fff;overflow:hidden;box-sizing:border-box;margin:2px
}
.highlight{opacity:.7;border:none;transform:scale(1);transition:all .5s ease-out
}
.highlight:hover{opacity:1;transform:scale(1.02)
}
.highlight.active{border:1px solid red;transition:none;opacity:1;transform:scale(1)
}
.carousel{width:100%
}
.carousel-cell{width:100%;height:160px;display:-ms-flexbox;display:flex
}
.carousel-cell .panel{-ms-flex-item-align:center;align-self:center;width:100%;height:75%;margin:0px 10px 40px;background:#f1dea1;box-sizing:border-box;color:black;border-radius:5px;box-shadow:0 4px 2px -2px rgba(128,128,128,0.5);display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center
}
.carousel-cell .panel img{max-width:100%;max-height:100%
}
.flickity-prev-next-button.previous,.flickity-prev-next-button.next{width:24px;height:24px;top:90%;z-index:2;background:#f3dfa0
}
.flickity-page-dots{top:85%;z-index:1
}
.flickity-page-dots>li.dot{margin:0 6px;background:#dcc26a;opacity:.4
}
.flickity-prev-next-button>svg>path.arrow{fill:#cf151b
}
@media all and (min-width: 801px){
.aside{-ms-flex:1;flex:1
}
.aside-1{-ms-flex-order:1;order:1
}
.main{-ms-flex-order:2;order:2
}
.aside-2{-ms-flex-order:3;order:3
}
.footer{-ms-flex-order:4;order:4
}
#mobile_buttons{display:none
}
}
@media only screen and (min-width: 600px) and (max-width: 800px){
h1{font-size:22px
}
#layout_title{display:none
}
.header{width:100vw;max-width:1366px;height:100px
}
#layout_block{margin:0;opacity:0;transition:opacity .5s ease-out;z-index:9;pointer-events:none;position:relative;background-color:rgba(251,247,216,0.9);background-color:pink;display:block;position:absolute;-ms-flex:none;flex:none;top:100px;left:0;width:64vw;height:64vw;margin:0;padding:0
}
#layout_block .transparent{height:100%;min-height:100%
}
#layout_block.m-active{opacity:1;z-index:400;pointer-events:auto
}
#main_block{display:block;position:relative;-ms-flex:none;flex:none;float:left;width:64vw;height:64vw
}
#photo_block{-ms-flex:none;flex:none;position:relative;width:29vw
}
.main{-ms-flex:2;flex:2
}
#photo_block{-ms-flex-order:2;order:2
}
.footer{-ms-flex-order:3;order:3
}
#mobile_buttons{position:absolute;top:44%;left:0;width:100%
}
#message_btn{display:none
}
#social_action{bottom:15px
}
#thanks h1{font-size:22px;padding:10px
}
}
@media (max-width: 599px){
.wrapper,.header{display:block
}
#main_block{height:100vw;display:block;padding:0px;margin-top:-100vw;z-index:2;position:relative
}
#mainStage{-ms-flex-item-align:auto;-ms-grid-row-align:auto;align-self:auto
}
#layout_title{display:none
}
#layout_block{padding:0;margin:0;width:100vw;height:100vw;opacity:0;transition:opacity .5s ease-out;z-index:9;pointer-events:none;position:relative;background-color:rgba(251,247,216,0.9)
}
#layout_block .transparent{height:100%;min-height:100%
}
#layout_block.m-active{opacity:1;z-index:400;pointer-events:auto
}
#mobile_buttons .social{width:40%;margin:8px 4px;display:inline-block
}
.carousel-cell{height:240px
}
#photo_block{position:relative;margin-top:-100vw;padding:0;z-index:1
}
#photo_block .transparent{height:100vw;min-height:100vw;padding:0;margin:0;opacity:0;transition:opacity .5s ease-out;pointer-events:none;background-color:rgba(251,247,216,0.9)
}
#photo_block.m-active{z-index:400
}
#photo_block.m-active .transparent{opacity:1;pointer-events:auto
}
#message_block{height:100%
}
.carousel{height:65%
}
.carousel p{font-size:20px
}
#photos_title{display:none
}
.aside{-ms-flex:1 auto;flex:1 auto
}
.footer{margin:10px 5px
}
.scene_scroll{height:100vw
}
.scroll{height:auto;width:100%;overflow-x:auto;overflow-y:auto
}
#layout_block figure{width:30%;height:30%
}
figure .highlight{opacity:1
}
#social_action{position:relative;bottom:auto
}
#social_action h1{padding:10px 0 0 0
}
#social_action a#dl{transform:translateY(6px);background-position-x:7px;padding:8px 6px 8px 35px
}
#social_action .social{width:30%;height:33px;margin:8px 4px;display:inline-block;font-size:12px;line-height:14px
}
.gallery_img{margin:2px;width:30%
}
.gallery_img figure{width:100%;height:100%
}
.img-gallery.scroll{height:80%
}
.social{text-shadow:none
}
#thanks{padding:0px;color:#383838
}
#thanks h1{font-size:18px;padding:6px
}
#thanks p{width:100%
}
}



